import React, {useMemo} from 'react';
import {
    DiGit,
    DiCss3,
    SiReact,
    VscRegex,
    IoLogoReact,
    AiFillHtml5,
    SiReactivex,
    SiTypescript,
    DiJqueryLogo,
    IoLogoAngular,
    IoFlameOutline,
    SiAdobephotoshop,
    IoLogoJavascript,
    AiOutlineFieldTime,
    BsFillBootstrapFill,
} from "react-icons/all";

import styles from "./Strength.module.scss";
import {Title} from "../Title/Title";

export const Strength = () => {
    const skills = useMemo(() => [
        {
            id: 1,
            icon: IoLogoJavascript,
            percent: 100,
            color: '#e9d44d',
            title: 'Javascript'
        },
        {
            id: 2,
            icon: IoLogoReact,
            percent: 90,
            color: '#5ed3f3',
            title: 'React'
        },
        {
            id: 3,
            icon: IoLogoAngular,
            percent: 90,
            color: '#d6002f',
            title: 'Angular 2+'
        },
        {
            id: 4,
            icon: SiReactivex,
            percent: 90,
            color: '#e9068c',
            title: 'RxJs'
        },
        {
            id: 5,
            icon: SiTypescript,
            percent: 90,
            color: '#2f74c0',
            title: 'TypeScript'
        },
        {
            id: 6,
            icon: SiAdobephotoshop,
            percent: 60,
            color: '#1e7de7',
            title: 'Photoshop'
        },
        {
            id: 7,
            icon: AiFillHtml5,
            percent: 100,
            color: '#dd4b25',
            title: 'HTML 5'
        },
        {
            id: 8,
            icon: DiCss3,
            percent: 100,
            color: '#016db4',
            title: 'CSS 3'
        },
        {
            id: 9,
            icon: BsFillBootstrapFill,
            percent: 90,
            color: '#6e12ef',
            title: 'Bootstrap'
        },
        {
            id: 10,
            icon: DiGit,
            percent: 90,
            color: '#e94e31',
            title: 'Git'
        },
        {
            id: 11,
            icon: DiJqueryLogo,
            percent: 100,
            color: '#1e75a8',
            title: 'jQuery'
        },
        {
            id: 12,
            icon: VscRegex,
            percent: 80,
            color: '#f75f03',
            title: 'RegEx'
        },
        {
            id: 13,
            icon: SiReact,
            percent: 100,
            color: '#5ed3f3',
            title: 'React-Native'
        },
        {
            id: 14,
            icon: SiReactivex,
            percent: 90,
            color: '#b42acb',
            title: 'NgRx'
        },
        {
            id: 15,
            icon: AiOutlineFieldTime,
            percent: 100,
            color: '#66c200',
            title: 'Fast Coding'
        }
    ], []);


    return (
        <div id={'strength'} className={styles.wrapper}>
            <div className={styles.container}>
                <Title text={'Strength'} Icon={IoFlameOutline}/>

                <div className={styles.wrap}>
                    <div className={styles.boxWrapper}>
                        <h3 className={styles.boxTitle}>MY SKILLS</h3>
                        <div className={styles.skills}>
                            {skills.map(skillItem => {
                                const Icon = skillItem.icon;
                                return (
                                    <div
                                        key={`${skillItem.id}`}
                                        className={styles.skillItem}>
                                        <div className={styles.titleWrapper}>
                                            <Icon size={30}/>
                                            <span className={styles.title}>{skillItem.title}</span>
                                        </div>
                                        <div className={styles.progressWrapper}>
                                            <div
                                                className={styles.progress}
                                                style={{
                                                    backgroundColor: skillItem.color,
                                                    width: `${skillItem.percent}%`
                                                }}
                                            />
                                        </div>
                                    </div>
                                )
                            })}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};