import React from 'react';

import styles from './Card.module.scss'

export const Card = ({title, link, image}) => {
    const openLink = () => {
        window.open(link)
    }

    return (
        <div className={`${styles.container} portfolio-card`} onClick={openLink}>
            <div className={styles.bgWrapper}>
                <div style={{backgroundImage: `url(${image})`}} className={styles.bg}/>
            </div>
            <div className={styles.title}>{title}</div>
        </div>
    );
};