import React, {useCallback, useEffect} from 'react';
import moment from 'moment'
import {gsap} from "gsap";
import {ScrollTrigger} from "gsap/ScrollTrigger";

import styles from './WorkExperience.module.scss'
import workExperience from '../../assets/files/workExperience.json';

export const WorkExperience = () => {
    const startFormat = 'YYYY MMM'
    const endFormat = 'YYYY DD MMM'
    const formatDate = useCallback((date, dateFormat) => date ? moment(date).format(dateFormat) : moment().format(dateFormat), [])

    const openLink = (link) => {
        if (link) {
            window.open(link)
        }
    }

    useEffect(() => {
        const tl = gsap.timeline();
        tl
            .from(".work-experience-item:nth-child(1)", {x: -200, opacity: 0})
            .from(".work-experience-item:nth-child(2)", {x: 200, opacity: 0})
            .from(".work-experience-item:nth-child(3)", {x: -200, opacity: 0})
            .from(".work-experience-item:nth-child(4)", {x: 200, opacity: 0})

        ScrollTrigger.create({
            animation: tl,
            trigger: '#about-work-experience',
            start: '-=500 center',
            end: `+=${document.querySelector('#about-work-experience').offsetHeight / 2}`,
            scrub: 1,
        })
    }, []);

    return (
        <div className={styles.container}>
            {workExperience.map(work => {
                return (
                    <div className={`${styles.listItem} work-experience-item`} key={`${work.id}`}>
                        <div className={styles.leftSide}>
                            <div className={styles.title}>{work.title}</div>
                            <span className={styles.date}>
                            <span>{formatDate(work.startDate, startFormat)}</span>
                            <span> </span>
                            <span>{formatDate(work.endDate, endFormat)}</span>
                        </span>
                        </div>
                        <div className={styles.rightSide}>
                        <span className={styles.projects}>
                        <div className={styles.title}>Projects</div>
                            <div className={styles.list}>
                                {work.projects.map(project => {
                                    const link = project.link
                                    const key = `${work.id}-${project.title}`;
                                    return (
                                        <span
                                            key={key}
                                            className={`${styles.project} ${link ? styles.hasLink : ""}`}
                                            onClick={() => openLink(link)}
                                        >
                                            {project.title}
                                        </span>
                                    )
                                })}
                            </div>
                        </span>
                        </div>
                    </div>
                )
            })}
        </div>
    );
};