import React, {useState} from 'react';

import styles from './Button.module.scss'

export const Button = ({text, style, className, progress, showProgress = false, onClick}) => {
    const [classes, setClasses] = useState([styles.button, className])

    const onMouseDown = () => {
        setClasses([styles.button, styles.pressed, className])
    }

    const onMouseUp = () => {
        setClasses([styles.button, className])
    }

    return (
        <div className={classes.join(' ')}
             style={style}
             onMouseDown={onMouseDown}
             onMouseUp={onMouseUp}
             onClick={onClick}>
            {showProgress && <div className={styles.progress} style={{width: `${progress}%`}}/>}
            <span className={styles.buttonText}>{text}</span>
        </div>
    );
};