import React, {useEffect} from 'react';
import {gsap} from "gsap";
import {ScrollTrigger} from "gsap/ScrollTrigger";

import styles from './Hero.module.scss';
import {HeroTextAnimated} from "../HeroTextAnimated/HeroTextAnimated";

gsap.registerPlugin(ScrollTrigger);

export const Hero = () => {
    useEffect(() => {
        ScrollTrigger.create({
            trigger: '#home',
            pin: true,
            pinSpacing: false
        })
    }, []);

    return (
        <div id={'home'} className={styles.wrapper}>
            <div
                id={'header'}
                className={styles.container}
                // style={{transform: `rotateX(${rotation}rad) translateY(${translateY}px)`}}
            >
                <div className={styles.textWrapper}>
                    <span className={styles.text}>Design</span>
                    <span className={styles.text}>and code</span>
                    <HeroTextAnimated/>
                    <span className={styles.text}>apps</span>
                </div>
            </div>
        </div>
    );
};